const gallery = document.querySelector('.gallery');
const markHtml = images

  .map(image => {
    return `
    <li class="gallery-item">
      <a class="gallery-link" href="${image.original}">
        <img
          class="gallery-image"
          src="${image.preview}"
          data-source="${image.original}"
          alt="${image.description}"
        />
      </a>
    </li>
  `;
  })
  .join('');

gallery.innerHTML = markHtml;
const galleryS = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
  showCounter: true,
});

galleryS.on('show.simplelightbox', function () {
  const caption = document.querySelector('.sl-caption');
  if (caption) {
    caption.style.visibility = 'hidden';
  }
});

galleryS.on('shown.simplelightbox', function () {
  setTimeout(() => {
    const caption = document.querySelector('.sl-caption');
    caption.style.visibility = 'visible';
  }, 250);
});
